import Vue from 'vue';
import i18n from '../../../i18n';
import paging from '../../../common/scripts/paging';
export default Vue.extend({
    i18n: i18n(),
    mixins: [
        paging({
            captureWindowScroll: true,
        }),
    ],
    props: {
        pagination: {
            required: true
        },
        extract: {
            required: false
        }
    },
    computed: {
        notes() {
            return this.extract ? this.extract(this.items) : this.items;
        }
    }
});
